body {
    margin: 0;
    padding: 0;
    font-style: normal;
    font-weight: 400;
    color: #000000;
    background-color: #f7f7f7;
}
h1, h2, h3, h4, h5, p {
    margin: 0;
}
ul {
    margin: 0;
    padding: 0;
}
img {
    display: block;
}
.container {
    max-width: 1440px;
    margin: 0 auto;
}
.list {
    display: flex;
    padding: 0;
    margin: 0;
}
li {
    list-style: none;
}
a {
    text-decoration: none;
}


/* HEADER */

.header {
    border-bottom: 1px solid #F2F2F2;
    background-color: #FFFFFF;
}
.header__container{
    padding: 20px 17px;
    display: flex;
    align-items: center;
    position: relative;
    
}
.header__link {
    font-family: 'Cardo', sans-serif;
    font-size: 17px;
    color: #000000;
}
.header__item:not(:last-child){
    margin-right: 20px;
}
.logo {
    position: absolute;
    transform: translate(-50%, 0);
    left: 50%;
}


/* HERO */

.hero {
    background-image: url(../images/hero/hero.jpg);
    background-repeat: repeat;
    background-size: cover;
    background-position: center;
    padding: 257px 0 300px 0;

    position: relative;
    background-color: #ffffff;
}
.hero__container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.hero_button_black {
    position: absolute;
    height: 50px;
    left: 20px;
    bottom: 3px;
    padding: 15px 30px;
    background: #050505;
    border-radius: 1px 1px 0px 0px;

    font-family: 'Cardo',sans-serif;
    font-size: 15px;
    text-align: center;
    color: #FFFFFF;
}
.hero__title {
    max-width: 807px;
    margin-bottom: 31px;

    font-family: 'Roboto',sans-serif;
    font-weight: 700;
    font-size: 27px;
    line-height: 38px;
    text-align: center;
    letter-spacing: 0.2px;
    color: #FFFFFF;
}

.hero_button_white {
    border: 1px solid #FFFFFF;
    box-sizing: border-box;
    text-align: center;

    font-family: 'Cardo';
    font-weight: 700;
    font-size: 20px;
    color: #FFFFFF;

    padding: 6px 40px;
}

/* ADVANTAGES */


.advantages__list {
    justify-content: space-evenly;
    align-items: center;
}
.advantages__item {
    display: flex;
    align-items: center;
}
.advantages__icon {
    width: 60px;
    margin-right: 15px;
}
.advantages__text {
    font-family: 'Roboto', sans-serif;
    font-size: 13px;
    line-height: 24px;
    color: #000000;
}

/* GALLERY */

.gallery__img {
    max-width: 360px;
    max-height: 320px;
}


/* SHOP */

.shop {
    padding: 115px 0 20px 0;
}
.shop__item:not(:last-child) {
    margin-bottom: 30px;
}
.shop__img {
    max-width: 960px;
    max-height: 542px;
}
.shop__item:nth-child(even){
    flex-direction: row-reverse;
}
.shop__wrapper {
    padding-top: 48px;
}
.shop_wrapper_right {
    text-align: right;
    margin-right: 38px;
}
.shop_wrapper_left {
    text-align: left;
    margin-left: 38px;
}
.shop__title {
    font-family: 'Roboto', sans serif;
    font-weight: 700;
    font-size: 33px;
    line-height: 56px;
    color: #000000;
}

.shop .shop__wrapper p {
    font-family: 'Cardo', sans-serif;
    color: #000000;
}
.shop__title {
    margin-bottom: 4px;
}
.shop__model {
    font-style: italic;
    font-size: 18px;
    margin-bottom: 22px;
}
.shop__price {
    font-size: 15px;
    margin-bottom: 16px;
    
}
.shop__description {
    font-size: 18px;
    color: #525252;
    margin-bottom: 24px;
}
.shop_description_first {
    margin-bottom: 99px;
}
.shop__sizes {
    margin-bottom: 47px;
}
.shop__link{
    font-family: 'Roboto',sans-serif;
    font-size: 17px;
    line-height: 22px;
    color: #000000;
    text-decoration: underline;
}
.shop__new {
    font-family: 'Cardo';
    font-style: italic;
    font-size: 14px;
    text-align: right;
    color: #BC1414 !important;
}

/* REVIEW */
.review {
    position: relative;
    padding-top: 22px;
    padding-bottom: 52px;
}

.review__title {
    font-family: 'Cardo' sans-serif;
    font-weight: 700;
    font-size: 30px;
    line-height: 39px;
    text-align: center;
    color: #3A3A3A;
    margin-bottom: 21px;
}

.review__subtitle {
    font-family: 'Cardo' sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 33px;
    text-align: center;
    color: #000000;
    margin-bottom: 37px;
}

.image-slider {
    width: 1140px;
}

.image-wrapper {
    display: flex;
    height: auto;
}

.slider-item {
    display: flex;
    text-align: center; 
    
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}

.slider-img {
    position: relative;
    display: block;
    max-width: 367px;
    max-height: 550px;
}

.slider-img__icon {
    position: absolute;
    top: 40px;
    right: 30px;
    display: flex;
    align-items: center;
}

.slider-img__icon img {
    width: 14px;
    height: 14px;
    margin-right: 4px;
}

.slider-img__text {
    font-family: 'Cardo' sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 28px;
    color: #FFFFFF;
}

.swiper-pagination {
    position: relative;
    margin-top: 30px;
    z-index: 99;
}

.swiper-pagination-bullet {
    width: 9px;
    height: 9px;
    line-height: 9px;
    background-color: #000000;
    border-radius: 0;
    text-align: center;
}
.second-slider {
    background-color: #f6f6f6;
}
.review__slide {
    padding: 42px 99px 20px 99px;
    text-align: center;
}

.review__text {
    margin-bottom: 37px;
}

.review__text {
    font-family: 'Cardo' sans-serif;
    font-style: italic;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    color: #020202;
}

.review__author {
    font-family: 'Cardo' sans-serif;
    font-weight: 400;
    font-size: 15px;
    line-height: 21px;
    color: #000000;
}

/* CONCEPT */
.concept{
    background-color: #f8f8f8;
    padding-top: 44px;
}

.concept__item{
    align-items: center;
    text-align: center;
    width: 360px;
    height: 370px;

    padding: 54px 34px 46px 34px;
}

.concept__item:nth-child(odd){
    background-color: #ffffff;
}

.concept__svg{
    
    margin-bottom: 29px;
    width: 95px;
    height: 95px;
        /* background-color: #000000; */
    fill:#000000;
}

.concept__title{
    font-family: 'Cardo';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 35px;
    margin-bottom: 12px;
} 

.concept__text{
    font-family: 'Cardo';
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 28px;
    margin-bottom: 28px;
} 

.concept__link{
    font-family: 'Cardo';
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 15px;
    text-decoration: underline;
    color: inherit;
}


/* COLLAB */

.collab{
    padding-top: 134px;
    padding-bottom: 63px;
    background-color: #f8f8f8;
}

.collab__text{
    font-family: 'Cardo';
    font-weight: 400;
    font-size: 20px;
    line-height: 26px;
    text-align: center;
    margin-bottom: 41px;
    
}

.collab__img{
    width: 1420px;
}


/* FOOTER */

.footer {
    background-color: #E9E9E9;
    padding: 55px 0 60px 0;
}
.footer__container {
    width: 1140px;
    margin: 0 auto;
}
.footer__container_link {
    display: grid;
    grid-template-columns: 155px 140px 135px 1fr;
    grid-template-rows: 1fr;
    grid-column-gap: 130px;
}
.footer__info {
    display: flex;
    flex-direction: column;
    text-align: center;
}
.footer .footer__info_title {
    margin-bottom: 28px;
    font-family: 'Cardo', sans-serif;
    font-size: 20px;
    text-align: center;
    text-transform: uppercase;
}
.footer .footer__info_link {
    margin-top: 13px;
    font-family: 'Cardo', sans-serif;
    font-size: 15px;
    text-align: center;
    color: inherit;
}
.footer .subscription {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
}
.footer .subscription__text {
    font-family: 'Cardo', sans-serif;
    font-size: 15px;
    text-align: center;
    margin-bottom: 13px;
}
.subscription__input {
    width: 275px;
    padding: 4px 2px 1px 1px;
    text-align: center;
    font-family: 'Cardo', sans-serif;
    font-size: 15px;
}
.subscription__images {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin-top: 31px;
}

.subscription__images img:last-child {
    margin-top: 17px;
}


.footer__container_additions {
    display: flex;
    justify-content: space-evenly;
    margin-top: 30px;
    align-items: center;
}
.footer .rights {
    display: flex;
    font-family: 'Cardo', sans-serif;
    font-size: 12px;
    text-align: center;
}
.footer .payment {
    display: flex;
}
.footer .addition {
    display: flex;
    font-family: 'Cardo',sans-serif;
    font-size: 12px;
    text-align: center;
    color: #000000;
}
.footer .addition span {
    margin: 0 3px;
}

